import { Button, Grid } from "@mui/material"
import { Link } from "react-router-dom"
function Home() {
    return (
        <div className ='container'>
            <div>
                <Grid container>
                    <Grid item xs={6}>
                       <h1 className='my-5 text-success fw-bold'>Welcome to Food Island</h1>
                       <p className='lead text-muted'>Lorem ipsum dolor sit, amet consectetur adipisicing elit. <br /> Facilis beatae sunt nulla maxime excepturi fugit alias <br /> blanditiis dicta ex ab eveniet unde, corrupti <br /> distinctio ipsum ratione saepe minima numquam at!</p>
                    </Grid>
                    <Grid item xs={6}>
                        <img src="https://designhooks.com/wp-content/uploads/2018/06/kitchen-scene-mockup1.jpg" className='img-fluid rounded-3 mt-4 w-75' alt="" />
                    </Grid>
                </Grid>
                <Link to ='/restaurent'>
                <Button variant ='contained' className='d-block mx-auto my-5 py-2'>Find Recipe</Button>
                </Link>
                
            </div>
        </div>
    )
}

export default Home
