import {
  Card,
  CardMedia,
  CardContent,
  Typography,
  Button,
  CardActions,
} from "@mui/material";
import { Link } from "react-router-dom";

function Meal(props) {
  const { strMeal, strInstructions, strMealThumb, idMeal } = props.meal;
  return (
    <div className="container mt-5">
      <Card sx={{ maxWidth: 345 }}>
        <CardMedia
          component="img"
          height="140"
          image={strMealThumb}
          alt="green iguana"
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="div">
            {strMeal}
          </Typography>
          <Typography variant="body2" color="text.secondary">
            {strInstructions.slice(0, 220)}
          </Typography>
        </CardContent>
        <CardActions>
          <Link to={`/recipe/${idMeal}`} className="d-block mx-auto fw-bold">
            <Button size="medium">Show Details</Button>
          </Link>
        </CardActions>
      </Card>
    </div>
  );
}

export default Meal;
