import React, { useEffect, useState } from "react";
import Meal from "./Meal";

function Restaurent() {
  const [text, setText] = useState("");
  const [meals, setMeals] = useState([]);
  useEffect(() => {
    fetch(`https://www.themealdb.com/api/json/v1/1/search.php?s=${text}`)
      .then((res) => res.json())
      .then((data) => setMeals(data.meals));
  }, [text]);
  const searchHandler = (e) => {
    const searchText = e.target.value;
    setText(searchText);
  };
  return (
    <div className="container">
      <h1 className="text-center text-primary fst-italic">
        Find your favorite recipe here
      </h1>
      <div className="text-center">
        <input
          onChange={searchHandler}
          type="text"
          className="form-control w-25 d-block mx-auto my-5"
          placeholder="Type Recipe Name"
        />
      </div>
      <div className="row row-cols-3">
        {meals?.map((meal) => (
          <Meal meal={meal} key={meal.idMeal} />
        ))}
      </div>
    </div>
  );
}

export default Restaurent;
