import React, { useEffect, useState } from "react";
import { useParams } from "react-router";

function Recipies(props) {
  const { rcid } = useParams();
  const [recipe, setRecipe] = useState({});

  useEffect(() => {
    fetch(`https://www.themealdb.com/api/json/v1/1/lookup.php?i=${rcid}`)
      .then((res) => res.json())
      .then((data) => setRecipe(data.meals[0]));
  });
  const {
    strMeal,
    strCategory,
    strArea,
    strInstructions,
    strMealThumb,
    strYoutube,
  } = recipe;
  return (
    <div className="container">
      <div
        className="card my-5 p-3 mx-auto rounded-3 shadow"
        style={{ width: "45rem" }}
      >
        <img
          src={strMealThumb}
          className="rounded-circle h-50 w-50 d-block mx-auto"
          alt={strMeal}
        />
        <div className="card-body">
          <h5 className="card-title text-primary fw-bold display-5 my-3">
            {strMeal}
          </h5>
          <h5 className="card-title fst-italic">Category: {strCategory}</h5>
          <h5 className="card-title fst-italic">Origin: {strArea}</h5>
          <h4 className="card-title mt-4 fw-bold">Instruction:</h4>
          <p className="card-text fst-italic lead">{strInstructions}</p>
          <a
            href={strYoutube}
            className="btn btn-primary d-block w-25 mx-auto py-3 rounded-pill fst-italic"
            target="_blank"
          >
            Watch on YouTube
          </a>
        </div>
      </div>
    </div>
  );
}

export default Recipies;
